}

.bg1 {
    width: 100vh;
    height: 50vh;
    background: linear-gradient(to bottom, transparent 50%, rgb(0, 0, 0) 50%),
                radial-gradient(circle closest-side, #e2fb00, rgb(248, 141, 70));
    border: 1px solid black;
    border-radius: 12px;
  }
  
  .bg2 {
    width: 100vh;
